	module.exports = {"data":{"markdownRemark":{"html":"<p>12時に、街は騒々しい爆竹の音を響かせた。 10階を見て、心の特別な状態があります。今年の爆竹は昨年よりも小さく、おそらくますます文明化しています。広州、北京などの地域では爆竹を禁止しています。</p>\n<p>いくつかのハイエンドの花火を見て、小さく、おそらく非常に高価です。人々の生活水準が向上しているとも言えます。私たちは数千の爆竹を置きました。私たちは文明化されていません。そのような機会には、人々は悲しみを感じる傾向があります。彼らが生きるほど、自分の人生の存在、世界が感じる強い外的な強さ、感情や感情なしで存在する能力を感じるほどです。強さは人間の理性と理想に依存せずに存在する力です。プレーンな人生では、人々はしばしば自分自身を忘れて、社会や世界に統合されていると感じます。しかし、世界が沸騰したとき、それは孤独で静かであることが発見されました。</p>\n<p>「モノポリー」と呼ばれるビデオゲームをプレイし、しばらく遊んで、土地、株式、建物のショッピングモール、ついに破産し、犬が追いかけた「ダ・キーン・キーン」という男に迷ったエスケープ、ガターに落ちる。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0029.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0029.md","frontmatter":{"title":"2月10日 木曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0029.md absPath of file >>> MarkdownRemark"}}

